<template>
    <div class="UserInfo">
        <!--loading-->
        <div class="loading" v-if="isLoading">
            <img src="../assets/loading.gif">
        </div>
         <!--content-->
        <div class="userInfomation" v-else>
            <section>
                <p>主页</p>
                <img :src="infos.avatar_url">
                <span>{{infos.githubUsername}}</span>
                <p>积分{{infos.score}}</p>
                <p>注册时间：{{infos.create_at | formatDate}}</p>
            </section>

            <div class="establish">
            <p>最近创建的话题</p>
            <ul>
                <li v-for="info in infos.recent_topics" :key="info.id">
                    <img :src="info.author.avatar_url">
                    <router-link :to="{name:'post_content',
                        params:{
                            id:info.id
                        }}">
                        <a>
                        {{info.title}}
                        </a>
                    </router-link>
                </li>
            </ul>
        </div>

        <div class="topics">
            <p>最近参与的话题</p>
            <ul>
                <li v-for="info in infos.recent_replies" :key="info.id">
                    <img :src="info.author.avatar_url">
                    <router-link :to="{name:'post_content',
                        params:{
                            id:info.id,
                        }}">
                        <a>
                        {{info.title}}
                        </a>
                    </router-link>
                </li>
            </ul>
        </div>
        </div >
         
        
    
        
    </div>
</template>

<script>
    export default {
        name:"Userinfo",
        data(){
            return {
               isLoading: false,
               infos:{}
            }
        },
        methods:{
            getInfo(){
                this.$http
                .get(`https://cnodejs.org/api/v1/user/${this.$route.params.author}`)
                .then((res)=>{
                    if(res.data.success==true){
                        this.isLoading =false;
                        this.infos = res.data.data;
                    }
                })
                .catch((err)=>{
                    console.log(err);
                })
            }
        },
        beforeMount(){
                this.isLoading = true;//加载成功之前显示加载动画
                this.getInfo();//在页面加载之前获取数据
            }

        
    }
</script>

<style scoped>
  .userInfomation {
    background: white;
    width: 75%;
    margin: 10px auto;
  }
  .userInfomation section {
    padding: 12px;
    font-size:14px; 
  }
  .userInfomation img {
    width: 30px;
  }
  .userInfomation li {
    list-style:none;
  }
  .userInfomation .establish,
  .userInfomation .topics {
    font-size: 14px;
    border-top: 10px #DDDDDD solid;
  }
  .userInfomation > div > p {
    padding: 12px 0 12px 12px;
    background-color: rgba(212, 205, 205, 0.17);
    font-size:14px;
    margin: 0;
  }
  .userInfomation > div >ul > li {
    padding: 4px 0 4px 12px;
    white-space: nowrap;
    font-size: 0.72rem;
    text-overflow: ellipsis;
    overflow: hidden;
    line-height: 30px;
    vertical-align: middle;
  }
  .userInfomation > div >ul > li > a {
    color: #094E99;
    text-decoration: none;
  }
</style>


