<template>
  <div class="autherinfo">

    <div class="authersummay" v-if="autherinfo.loginname">
      <p class="topbar">个人信息</p>
      <router-link
        :to="{
            name:'userinfo',
            params:{
                author:autherinfo.loginname
            }
        }"
      >
        <img :src="autherinfo.avatar_url"> 
      </router-link>
      <span>{{autherinfo.loginname}}</span>
    </div>

    <div class="recent_topics">
      <p class="topbar">作者其它话题</p>
      <div >
          <ul>
            <li v-for="info in autherinfo.recent_topics" :key="info.id">
              <router-link 
              :to="{
                name:'post_content',
                params:{
                 id:info.id,
                 author:info.author.loginname
                }}">
                {{info.title}}
              </router-link>
            </li>
          </ul>
      </div>
    
    </div>
    <div class="recent_replies">
      <p class="topbar">最近回复的话题</p>
      <div>
         <ul>
            <li v-for="info in autherinfo.recent_replies" :key="info.id">
              <router-link 
              :to="{
                name:'post_content',
                params:{
                 id:info.id,
                 author:info.author.loginname
                }}">
                {{info.title}}
              </router-link>
            </li>
         </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "sidebar",
  data() {
    return {
        autherinfo: {}
    }
  },
  methods: {
    getAutherInfo() {
      this.$http
        .get(`https://cnodejs.org/api/v1/user/${this.$route.params.author}`)
        .then(res => {
          this.autherinfo = res.data.data;
          console.log(this.autherinfo);
        })
        .catch(err => {
          console.log(err);
        });
    }
  },
  beforeMount() {
    this.getAutherInfo();
  }
  
};
</script>

<style scoped>

.authersummay,
.recent_replies,
.recent_topics {
  background-color: #fff;
}
.autherinfo {
  width: 328px;
  float: right;
  margin-top: 0;
  border: 1px solid salmon;
}
li {
  padding: 3px 0;
}
.recent_replies ul,
.recent_topics ul {
  margin-top: 0px;
  margin-bottom: 0px;
  list-style: none;
  padding-left: 14px;
}

ul a {
  font-size: 12px;
  text-decoration: none;
  color: #778087;
}

.topbar {
  padding: 10px;
  background-color: #f6f6f6;
  height: 16px;
  font-size: 12px;
  margin-top: 10px;
}

img {
  height: 48px;
  width: 48px;
  border-radius: 3px;
  margin: 10px;
}

.loginname {
  width: 100px;
  float: right;
  margin-top: 22px;
  margin-right: 159px;
  font-size: 14px;
}

.loginname a {
  text-decoration: none;
  color: #778087;
}

.authersummay .topbar {
  margin-top: 0px;
}
</style>


