<template>
  <div class="PostList">
    <!--loading-->
    <div class="loading" v-if="isLoading">
      <img src="../assets/loading.gif">
    </div>
   
    <div class="posts" v-else>
<!--分类-->
      <ul>
        <li>
          <div class="toobar">
            <span>全部</span>
            <span>精华</span>
            <span>分享</span>
            <span>问答</span>
            <span>招聘</span>
          </div>
        </li>
<!--帖子-->
        <li v-for="post in posts" :key="post.id" >
          <!--头像-->
          <img :src="post.author.avatar_url">
          <!--回复/浏览-->
          <span class="allcount">
            <span class="reply_count">{{post.reply_count}}</span>
            /{{post.visit_count}}
          </span>
          <!--帖子的分类-->
          <span :class="[{put_good:(post.good === true),put_top:(post.top  === true),
                      'topiclist-tab':(post.good  !== true && post.top  !== true)}]"> 
                {{post | tabFormatter}}
          </span>
          <!--标题-->
          <router-link :to="{name:'post_content',
              params:{
                id:post.id,
                author:post.author.loginname
              }}">
              <a>
                {{post.title}}
              </a>
          </router-link>
          <!--最終回复时间-->
          <span class="last_reply">{{ post.last_reply_at | formatDate}}</span>
        </li>
        <li>
          <pagination @turn="upDate"></pagination>  
        </li>
      </ul>
    </div>

   
  </div>
</template>

<script>
import pagination from "./Pagination"

export default {
  name: "Postlist",
  data: function() {
    return {
      isLoading: false,
      posts: [],
      turnpage:1
    }
  },
  components:{
    pagination
  },
  methods: {
    getArticleData: function() {
      this.$http
        .get("https://cnodejs.org/api/v1/topics", {
          params: {
            page: this.turnpage,
            limit: 20
          }
        })
        .then(response => {
          this.isLoading = false;
          this.posts = response.data.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    upDate(value){
       this.turnpage = value;
       this.getArticleData();  //!
    }
  
  },
  
  beforeMount() {
    this.isLoading = true;
    this.getArticleData();
  }
};
</script>

<style scoped>

.PostList {
  background-color: white;
  border-radius: 5px;
}
.PostList img {
    height: 30px;
    width: 30px;
    vertical-align: middle;
  }

.toobar {
  height: 50px;
  padding-left: 10px;
  border-radius:5px 5px 0px 0px; 
  background-color: #f5f5f5;
}

.toobar span {
  font-size: 14px;
  color: #80bd01;
  line-height: 50px;
  margin: 0 15px;
  cursor: pointer;
}
.toobar span:hover {
  color: #9e78c0;
}
.toobar span:active {
  color: red;
}

.allcount {
  width: 70px;
  display: inline-block;
  text-align: center;
  font-size: 12px;
}
.reply_count {
color: rgb(112, 168, 28);
font-size: 14px;
}
.put_good, .put_top {
    background: #80bd01;
    padding: 2px 4px;
    border-radius: 3px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    -o-border-radius: 3px;
    color: #fff;
    font-size: 12px;
    margin-right: 12px;
  }
.topiclist-tab {
    background-color: #e5e5e5;
    color: #999;
    padding: 2px 4px;
    border-radius: 3px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    -o-border-radius: 3px;
    font-size: 12px;
    margin-right: 12px;
}
.last_reply {
    text-align: right;
    min-width: 50px;
    /* display: inline-block; */
    white-space: nowrap;
    float: right;
    color: #778087;
    font-size: 12px;
  }
ul {
  list-style: none;
  width: 100%;
  max-width: 1344px;
  margin: 0 auto;
}
ul li:not(:first-child) {
  padding: 9px;
  font-size: 15px;
  font-family: "Helvetica Neue", "Luxi Sans", "DejaVu Sans", Tahoma, "Hiragino Sans GB", STHeiti, sans-serif !important;
  font-weight: 400;
  background-color: white;
  color: #333;
  border-top: 1px solid #f0f0f0;
}
li:not(:first-child):hover {background: #f5f5f5;}
li:last-child:hover { background: white;}
li span {line-height: 30px;}


a {
    text-decoration: none;
    color: black;
  }

  a:hover {
    text-decoration: underline;
  }

  .loading {
    text-align: center;
    padding-top: 300px;
  }


</style>


