<template>
  <div class="pagination">
    <button @click="changeBtn">首页</button>
    <button @click="changeBtn">上一页</button>
    <button v-if="judge" class="pagebtn">......</button>

    <button v-for="index in pagenumbers" :key="index.id"
    :class="[{'currentPage':index==currentpage},'pagebtn']"
    v-on:click="changeBtn(index)"
    >
        {{index}}
    </button>

    <button @click="changeBtn">下一页</button>
  </div>
</template>

<script>
import $ from 'jquery'

export default {
  name: "pagination",
  data() {
    return {
      pagenumbers: [1, 2, 3, 4, 5, "......"],
      currentpage: 1,
      judge:false
    }
  },
  methods: {
    changeBtn(a){

        if(typeof a != 'number'){                //?
            switch(a.target.innerText){
                case "上一页":
                  $('button.currentPage').prev().click();
                break;
                case "下一页":
                  $('button.currentPage').next().click();
                break;
                case '首页':
                 this.pagenumbers = [1,2,3,4,5,'......'];
                 this.changeBtn(1);
                break;
              default:
                break;
            }
            return;                     //?
        }
    
        this.currentpage = a;

        if(a>4){
           this.judge=true;
        }else{
            this.judge=false;
        }

        if(a == this.pagenumbers[4]){
            this.pagenumbers.shift();
            this.pagenumbers.splice(4,0,this.pagenumbers[3]+1);
        }else if(a == this.pagenumbers[0] && a!=1){
            this.pagenumbers.unshift(this.pagenumbers[0]-1);
            this.pagenumbers.splice(5,1);

        }
        this.$emit('turn',this.currentpage)





    }
  }
}
</script>

// 今天费了一下午的劲，终于在vue-cli 生成的工程中引入了jquery，记录一下。(模板用的webpack)
