// new webpack.ProvidePlugin({
// jQuery: "jquery",
// $: "jquery"
// })
// ]

// 然后一定要重新 run dev
// 在main.js 引入就ok了import $ from 'jquery'

<style scoped>
.pagination {
  margin-top: 5px;
  margin-bottom: 20px;
  background-color: rgb(128, 243, 74);
  padding: 6px 20px;
  border-radius: 5px;
}

button {
  background-color: #fff;
  border: 1px solid #ddd;
  color: #778087;
  border-radius: 3px;
  outline: none;
  height: 21px;
  cursor: pointer;
  padding: 0 2px;
  width: 55px;
  height: 29px;
}

.pagebtn {
  position: relative;
  bottom: 1px;
  width: 40px;
  margin: 0 4px;
  background-color: orange;
}

.currentPage {
  color: white;
  background-color: #1f1b1b;
}
</style>


