<template>
  <div class="article">
    <!--loading-->
    <div class="loading" v-if="isLoading">
      <img src="../assets/loading.gif">
    </div>
    <!--content-->
    <div v-else>
      <div class="topic_header">
        <div class="topic_title">{{post.title}}</div>
        <div class="mian">
          <span>·发布于：{{post.create_at | formatDate}}</span>
          <span>·作者：{{post.author.loginname}}</span>
          <span>·{{post.visit_count}}次浏览</span>
        </div>
        <div v-html="post.content" class="topic_content"></div>
      </div>
    </div>
    <!--回复-->
    <div id="reply">
      <div class="topbar">回复</div>
      <div v-for="(reply,index) in post.replies" :key="reply.id" class="replySec">
        <div class="replyUp">
          <router-link
            :to="{
                  name:'userinfo',
                  params:{
                      author:reply.author.loginname
                  }
                }"
          >
            <img :src="reply.author.avatar_url">
            <span>{{reply.author.loginname}}</span>
          </router-link>
           <span>{{index+1}}楼</span>
           <span v-if="reply.ups.length>0">☝ {{reply.ups.length}}</span>
        </div>

        <div>
          <p v-html="reply.content" class="reply_content"></p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Article",
  data() {
    return {
      isLoading: false,
      post: {}
    };
  },
  methods: {
    getData: function() {
      this.$http
        .get(`https://cnodejs.org/api/v1/topic/${this.$route.params.id}`)
        .then(res => {
          if (res.data.success == true) {
            this.isLoading = false;
            this.post = res.data.data;
          }
        })
        .catch(err => {
          console.log(err);
        });
    }
  },
  beforeMount() {
    this.isLoading = true;
    this.getData();
  },
  watch:{
    '$route'(to,from){
      this.getData()
    }
  }
};
</script>

<style>
@import url("../assets/markdown-github.css");
.topbar {
  padding: 10px;
  background-color: #f6f6f6;
  height: 16px;
  font-size: 12px;
  margin-top: 10px;
}

.article:not(:first-child) {
  margin-right: 340px;
}

#reply,
.topic_header {
  background-color: #fff;
}

#reply {
  margin-top: 15px;
}

#reply img {
  width: 30px;
  height: 30px;
  position: relative;
  bottom: -9px;
}

#reply a,
#reply span {
  font-size: 13px;
  color: #666;
  text-decoration: none;
}
.replySec {
  border-bottom: 1px solid #e5e5e5;
  padding: 0 10px;
}

.loading {
  text-align: center;
  padding-top: 300px;
}

.replyUp a:nth-of-type(2) {
  margin-left: 0px;
  display: inline-block;
}

.topic_header {
  padding: 10px;
}

.topic_title {
  font-size: 20px;
  font-weight: bold;
  padding-top: 8px;
}

.topic_header span {
  list-style: none;
  padding: 0px 0px;
  margin: 6px 0px;
  display: inline-block;
  font-size: 12px;
  color: #838383;
}

.topic_content {
  border-top: 1px solid #e5e5e5;
  padding: 0 20px;
  
}

.markdown-text img {
  width: 90% !important;
}

.reply_content{
  font-size: 14px;
  padding-top: 10px;
  color:rgb(75, 75, 75);
  /* max-width: 500px; */
  /* border:1px solid green; */
}
.reply_content code{
  width: 80%;
  word-break: break-all;
  border:1px solid orange;
}
</style>


